.contact{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: calc(35vh - 64px);
    text-align: center;
    background: #482c3d;
    color: #fff;
}
.contact h2{
    padding-top: 30px;
}
.contactIcons{
    width: 250px;
    height: auto;
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
}
.emailCtnr{
    display: flex;
    justify-content: center;
    width: 200px;
    height: auto;
    background: #fff;
    border-radius: 2px;
    border: #000 solid 1px;
}
.emailCtnr p{
    margin: 0;
    padding: 10px;
}